import Loading from '../../public/Loading'
import lan  from '../../../static/js/language'
import test from '../../../static/js/sex'
import Head from '../../public/head'
import None from '../../public/noNumber'
  export default {
      name: "appliCation",
    data(){
        return{
          api: this.GLOBAL.baseURL,
          volunList:[],
          activity_id:'',
          total:0,
          content:'',
          languageList:[
          ],
          sexArr:[],
          isLoading:false
        }
    },
    components:{
      Head,
      None,
      Loading
    },
    methods:{
      goBack(){
        this.$router.go(-1)
      },
      getEroll(val){
        this.isLoading = true
        this.$http.post(this.api + '/ErollL',{
          token: localStorage.getItem('token'),
          activity_id:this.activity_id,
          page: val
        })
          .then(res=>{
            if(res.data.code == 1){
              this.volunList = res.data.data.data
              this.total = res.data.data.total
              this.isLoading = false
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/Index/token')
                .then(res=>{
                  if(res.data.code == 1){
                    localStorage.setItem('token',res.data.data)
                  }
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
              this.isLoading = false
            }
          })
      },
      handleCurrentChange(val){
        this.getEroll(val)
      },
      takeUp(item,index){
        let a = ''
        let b = this.languageList[item.main_language].label + ','
        for(let i = 0;i<item.language.length;i++){
          a += this.languageList[item.language[i]].label+','
        }
        a = a +'/'+ item.skill
        this.content =b+ a
      },
      changeErollA(item,val){
        this.isLoading = true
       this.$http.post(this.api + '/ErollA',{
         token: localStorage.getItem('token'),
         enroll_id: item.enroll_id,
         flag: val
       })
         .then(res=>{
           if(res.data.code == 1){
             item.audit = val == 1? 1:2
             this.isLoading = false
           }else if(res.data.code == 3){
             this.$http.post(this.api + '/home/index/token')
               .then(res=>{
                 if(res.data.code == 1){
                   localStorage.setItem('token',res.data.data)
                   this.changeErollA(item,val)
                 }
               })
           }else if(res.data.code == 0){
             alert(res.data.msg)
             this.isLoading = false
           }
         })
      }
    },
    mounted(){
      this.getEroll(1)
    },
    created(){
        this.languageList = lan()
      this.sexArr = test()
      this.activity_id = this.$route.query.information
    }
  }



